import React from 'react';
import classes from './topbar.module.css';
import ProductData from './ProductData';

function TopBar(props){
    return(
        <div className={classes.topbar}>
        <img src={ProductData.logoimg} alt='logo' />
        <button style={props.dm ?{... {backgroundColor: 'white'},...{color:'black'}}:{...{backgroundColor: 'black'},...{color:'white'}}} onClick={props.darkmodeclk}>{props.displaydark}</button>
        </div>);
}
export default TopBar;