import React from 'react';
import classes from './sideBar.module.css';


function SideBar(props){
    const mapper=props.data.productOptions.map((item,pos)=>
  {return(
    <button style={
        {backgroundColor:props.dm ? 'black' : 'white'}
      }
     key={pos} onClick={()=>props.onbtnclick(pos)}>
    <img key={pos} src={props.data.productOptions[pos].imageUrl[0]} alt={props.data.productOptions[pos].imgTitle}/>
    <p style={{color:props.dm ? 'white' : 'black'}}>{props.data.productOptions[pos].imgTitle}</p>
    </button>
  );})
    return(
        <div>
        <div className={classes.sideBar}>
      <div className={classes.listSize}> 
       {mapper}
      </div>
      <div className={classes.cartSize}></div>
      </div>
      </div>
    );
}
export default SideBar;