import React ,{ Component }  from 'react';
import classes from  './App.module.css';

import ProductData from './ProductData';
import ProductViewer from './ProductViewer';
import SideBar from './sideBar';
import CartViewer from './cartViewer';


import TopBar from './topBar';

class App extends Component {
  state={
    pdata:ProductData,
    currentproduct:0,
    currentproductimage:0,
    forrender:0,
    darkmode:false,
    dmbtntext:'Darkmode'
  }

  mapper=this.state.pdata.productOptions.map((item,pos)=>
  {return(
    <button>
    <img key={pos} src={this.state.pdata.productOptions[pos].imageUrl[0]} alt={this.state.pdata.productOptions[pos].imgTitle}/>
    <p>{this.state.pdata.productOptions[pos].imgTitle}</p>
    </button>
  );})
  onbtnclick = (pos) =>{
    this.setState({currentproduct:pos,currentproductimage:1
    })
  }
  onabbtnclick = (pos) =>{
    if (this.state.currentproductimage <=0  ){
      this.setState({currentproductimage:0})
   }
   else{
    const upcpi=this.state.currentproductimage -1
    this.setState({currentproductimage:upcpi}) }}
   
  
  onafbtnclick = (pos) =>{
    if (this.state.currentproductimage >=(this.state.pdata.productOptions[pos].imageUrl.length)-1  ){
       this.setState({currentproductimage:(this.state.pdata.productOptions[pos].imageUrl.length)-1})
    }
    else{
    const upcpifb=this.state.currentproductimage +1;
    this.setState({currentproductimage:upcpifb})}
    
  }
  cartbtnclick = (pos) =>{
    ProductData.productOptions[pos].insidecart=true
    this.setState({forrender:this.state.forrender++})
  }
  deletebtnclick =(pos) =>{
    ProductData.productOptions[pos].insidecart=false
    this.setState({forrender:this.state.forrender++})

  }

  darkmodeclk =()=>{
    if (this.state.darkmode === false){
      this.setState({darkmode:true})
      this.setState({dmbtntext:'Light mode'})
    }
    else{
      this.setState({darkmode:false})
      this.setState({dmbtntext:'Dark mode'})
    }
  }
  
  
  render(){
  return (
    console.log(this.state.darkmode),
    console.log(this.state.dmbtntext),
    <body style={
      {backgroundColor:this.state.darkmode? 'black' : 'white'}
    }>
    <div className={classes.App}>
      <TopBar  darkmodeclk={this.darkmodeclk}  displaydark={this.state.dmbtntext} dm={this.state.darkmode} />
      <div className={classes.productSize}>
      <ProductViewer currentproduct={this.state.pdata.productOptions[this.state.currentproduct].imageUrl[this.state.currentproductimage]}
                    currentproducttitle={this.state.pdata.productOptions[this.state.currentproduct].imgTitle}
                    currentproductdes={this.state.pdata.productOptions[this.state.currentproduct].description}
                    cartbtnclick={this.cartbtnclick}
                    onabbtnclick={this.onabbtnclick}
                    onafbtnclick={this.onafbtnclick}

                    pos={this.state.currentproduct}/>
      <CartViewer data={this.state.pdata} cartbtnclick={this.cartbtnclick} deletebtnclick={this.deletebtnclick}/>
      </div>
      <div className={classes.sideBar} >
      <SideBar data={this.state.pdata} onbtnclick={this.onbtnclick} dm={this.state.darkmode}/>
      
      </div>
    </div>
    </body>
  );
  }
}

export default App;
