import React from 'react';
import ProductData from './ProductData';
import classes from './ProductViewer.module.css';

function ProductViewer(props) {
    
    return(
        <div className={classes.ProductViewer}>
          <div>
          <img src={props.currentproduct} alt='imagepro'/>
          <button onClick={()=>props.onabbtnclick(props.pos)}  ><img src={ProductData.arrowback}  alt='arrowback'/></button>
          <button onClick={()=>props.onafbtnclick(props.pos)}><img src={ProductData.arrowfront} alt='arrowfront'/></button>
          </div>
          <div >
            <h1>{props.currentproducttitle}</h1>
            <p>{props.currentproductdes}
            </p>
            <button onClick={()=>props.cartbtnclick(props.pos)}> Add to cart</button>
          </div>
        </div>
    );
}

export default ProductViewer;
