import React from 'react';
import classes from './cartviewer.module.css';
import deletebtn from './images/deletebtn.png';
import ProductData from './ProductData';


const  CartViewer=(props)=>{
    //const [list,setlist]= React.useState{ProductData};
    var count=0;

    const cartmapper=props.data.productOptions.map((item,pos)=>
     {  if (ProductData.productOptions[pos].insidecart){
      return(
      <div key={pos}>
    <img key={pos} src={props.data.productOptions[pos].imageUrl[0]} alt={props.data.productOptions[pos].imgTitle}/>
    <p>{props.data.productOptions[pos].imgTitle}</p> <button onClick={count++,()=> props.deletebtnclick(pos)}><img src={deletebtn} alt='delete'/></button><hr /></div>);}
    else {
        return null
    }
  })
    return (
        <div className={classes.cartviewer}>

            <h2>Your Cart :({count})</h2>
           
            {cartmapper}
            
            
        </div>
    )
}

export default CartViewer;